/* ===============================
   Modern CSS Reset / Full Reset
   =============================== */
/* Box-sizing border-box for all elements */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block;
}

/* Set default body styles */
html,
body {
  height: 100%;
  font-family: sans-serif;
  scroll-behavior: smooth;
  background: #fff;
  color: #000;
}

/* Remove list styles on ul, ol elements */
ul,
ol,
li {
  list-style: none;
  text-decoration: none;
}

/* Remove default quotes for blockquote, q */
blockquote,
q {
  quotes: none;
}

blockquote::before,
blockquote::after,
q::before,
q::after {
  content: "";
  content: none;
}

/* Remove table spacing and borders */
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Remove default button/input styles */
button,
input,
select,
textarea {
  font: inherit;
  background: none;
  border: none;
  outline: none;
  color: inherit;
}

/* Anchor tag reset */
a {
  text-decoration: none;
  color: inherit;
}

/* Image reset */
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
  height: auto;
}

/* Form elements inherit font */
input,
button,
textarea,
select {
  font: inherit;
}

/* Optional: smooth scroll behavior for the whole page */
html {
  scroll-behavior: smooth;
}

body {
  display: flex;
  justify-content: center;
  background-color: #12121c;
  color: white;
  min-height: 100vh;
}

main {
  width: 100%;
}

.wrapper {
  display: flex;
  width: 100%;
  max-width: 1440px;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  padding-inline: 90px;
}
@media (max-width: 768px) {
  .wrapper {
    padding-inline: 32px;
  }
}

[class*=__container] {
  padding-block: 120px;
}
@media (max-width: 1024px) {
  [class*=__container] {
    padding-block: 100px;
  }
}
@media (max-width: 414px) {
  [class*=__container] {
    padding-block: 45px;
  }
}

.hero__container {
  display: flex;
  flex-direction: column;
  gap: 76px;
}

.hero__digital {
  max-width: 1093px;
}

.hero__digital-title {
  font-family: "Sora", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 72px;
  line-height: 86px;
  letter-spacing: 0px;
  padding-block-end: 16px;
}

.hero__digital-title--product {
  color: #88888d;
}

.hero__digital-title--point {
  color: #57efb4;
}

.hero__digital-text {
  font-family: "Sora", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -1%;
  color: #88888d;
  padding-block-end: 32px;
  max-width: 809px;
}

.button {
  text-align: center;
  background-color: #006ada;
  max-width: 107px;
  font-family: "DM Sans", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0px;
  padding-block: 14px;
  padding-inline: 24px;
  border-radius: 8px;
  white-space: nowrap;
}

.hero__cards {
  display: grid;
  place-items: center;
  grid-template-columns: repeat(3, 1fr);
  gap: 35px;
}
@media (max-width: 1025px) {
  .hero__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 768px) {
  .hero__cards {
    grid-template-columns: repeat(1, 1fr);
  }
}

.hero__card {
  display: flex;
  flex-direction: column;
  padding: 20px;
  max-width: 402px;
}

.hero__card--grey {
  background-color: #222233;
  border-radius: 8px;
}

.hero__card-icon {
  max-width: 44px;
  max-height: 44px;
  padding: 15px;
  border-radius: 8px;
  background-color: #222233;
}
.hero__card-icon img {
  width: 15px;
  height: 15px;
}
.hero__card-icon--green {
  background-color: #57efb4;
}

.hero__card-title {
  padding-block-start: 24px;
  padding-block-end: 16px;
  font-family: "Sora", sans-serif;
  font-weight: 600;
  font-style: normal;
  font-size: 24px;
  line-height: 32px;
}

.hero__card-title-text {
  font-family: "Sora", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0px;
  color: #88888d;
}

.header {
  width: 100%;
  max-width: 1440px;
}

.nav {
  display: flex;
  align-items: center;
  gap: 20px;
}

.logo {
  display: flex;
  align-items: center;
  gap: 8px;
  flex: 1 1;
}
@media (max-width: 768px) {
  .logo {
    padding-block: 25px;
  }
}

.logo__img {
  width: 44px;
  height: 44px;
}
@media (max-width: 768px) {
  .logo__img {
    width: 38px;
    height: 38px;
  }
}

.logo__text {
  font-family: "DM Sans", sans-serif;
  font-weight: 700;
  font-size: 27px;
  line-height: 100%;
  letter-spacing: 0px;
}
@media (max-width: 768px) {
  .logo__text {
    font-size: 22px;
  }
}

.nav__list {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 40px;
  padding-block: 36px;
}
@media (max-width: 768px) {
  .nav__list {
    display: none;
  }
}

.nav__link {
  font-family: "Sora";
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0px;
  color: #88888d;
}
.nav__link:hover {
  color: #ffffff;
}

.nav__link--template {
  font-family: "DM Sans", sans-serif;
  font-weight: 700;
  color: #ffffff;
  font-size: 14px;
  line-height: 20px;
  padding-block: 14px;
  padding-inline: 24px;
  border-radius: 8px;
  background-color: #222233;
}

.burger {
  display: none;
  width: 26px;
  height: 18px;
}
@media (max-width: 768px) {
  .burger {
    display: flex;
    flex-direction: column;
    position: relative;
    gap: 6px;
    cursor: pointer;
  }
}
.burger::before, .burger::after,
.burger span::before {
  position: absolute;
  content: "";
  width: 26px;
  height: 2px;
  background-color: #ffffff;
}
.burger::before {
  top: 0;
}
.burger span::before {
  bottom: 0;
}
.burger::after {
  bottom: 8px;
}